.grid {
  width: 100%;
  display: block;
  padding: 0;
}

.grid.wide {
  max-width: 1200px;
  margin: 0 auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -4px;
  margin-right: -4px;
}

.row.no_gutters {
  margin-left: 0;
  margin-right: 0;
}

.col {
  padding-left: 4px;
  padding-right: 4px;
}

.row.no_gutters .col {
  padding-left: 0;
  padding-right: 0;
}

.c0 {
  display: none;
}

.c1 {
  flex: 0 0 8.33333%;
  max-width: 8.33333%;
}

.c2 {
  flex: 0 0 16.66667%;
  max-width: 16.66667%;
}

.c3 {
  flex: 0 0 25%;
  max-width: 25%;
}

.c4 {
  flex: 0 0 33.33333%;
  max-width: 33.33333%;
}

.c5 {
  flex: 0 0 41.66667%;
  max-width: 41.66667%;
}

.c6 {
  flex: 0 0 50%;
  max-width: 50%;
}

.c7 {
  flex: 0 0 58.33333%;
  max-width: 58.33333%;
}

.c8 {
  flex: 0 0 66.66667%;
  max-width: 66.66667%;
}

.c9 {
  flex: 0 0 75%;
  max-width: 75%;
}

.c10 {
  flex: 0 0 83.33333%;
  max-width: 83.33333%;
}

.c11 {
  flex: 0 0 91.66667%;
  max-width: 91.66667%;
}

.c12 {
  flex: 0 0 100%;
  max-width: 100%;
}

.co1 {
  margin-left: 8.33333%;
}

.co2 {
  margin-left: 16.66667%;
}

.co3 {
  margin-left: 25%;
}

.co4 {
  margin-left: 33.33333%;
}

.co5 {
  margin-left: 41.66667%;
}

.co6 {
